<template>
  <div>
    <el-row :gutter="60">
      <el-col :span="5" :offset="2">
        <div>
          <el-row>
            <p><el-input type="textarea" :rows="3" v-model="symptom" placeholder=""></el-input></p>
          </el-row>
          <el-row>
            <p><el-input type="textarea" :rows="3" v-model="disease" placeholder=""></el-input></p>
          </el-row>
          <el-row>
            <p>
              <el-select v-model="selectedCureItem" placeholder="">
                <el-option v-for="item in cureItem" :key="item.value" :label="item.label" :value="item.value"></el-option>
              </el-select>
            </p>
            <p>
              <el-select v-model="drugs" multiple filterable remote reserve-keyword placeholder="" :remote-method="remoteMethod" :loading="loading">
                <el-option v-for="item in drugInfo" :key="item.value" :label="item.label" :value="item.value"></el-option>
              </el-select>
            </p>
            <p>
              <el-input v-model="num" placeholder=""></el-input>
            </p>
          </el-row>
          <el-row>
            <el-button type="primary" @click="dialogVisible = true"></el-button>
            <el-dialog title="" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
              <span></span>
              <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false"> </el-button>
                <el-button type="primary" @click="dialogVisible = false;goInput()"> </el-button>
              </span>
            </el-dialog>
          </el-row>
        </div>
      </el-col>
      <el-col :span="14">
        <div>
          <el-card>
            <router-view/>
          </el-card>
        </div>
      </el-col>
  </el-row>
  </div>
</template>
<script>
export default {
  data () {
    return {
      cardid: this.$route.query.cardid,
      loadingHistory: true,
      size: 5,
      currentPage: 0,
      message: '',
      drugInfo: [ // 
        {
          drug_id: '',
          dr_name: '',
          dr_num: ''
        }
      ],
      drugs: [], // 
      symptom: '',
      disease: '',
      num: '',
      dialogVisible: false,
      dialogVisible1: false,
      cureItem: [
        {
          value: '',
          label: ''
        }, {
          value: 'CT',
          label: 'CT'
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }
      ],
      selectedCureItem: '', // 
      loading: false
    }
  },
  mounted () {
    this.getAllDrugs()
    this.$router.push({path: '/Cure/Advice', query: {cardid: this.cardid}})
  },
  methods: {
    remoteMethod (query) {
      if (query !== '') {
        this.loading = true
        this.getAllDrugInfo(query)
        setTimeout(() => {
          this.loading = false
          this.drugInfo = this.list.filter(item => {
            return item.label.toLowerCase()
              .indexOf(query.toLowerCase()) > -1
          })
        }, 200)
      } else {
        this.drugInfo = []
      }
    },
    formatRole (row, column) { //  
      return row.e_sex === '1' ? '' : row.e_sex === '0' ? '' : ''
    },
    handleClose () {
      this.dialogVisible = false
    },
    getAllDrugInfo (query) {
      var t = this
      this.$axios({
        method: 'get',
        url: '/Drug/findDrugByName?query=' + query
      })
        .then(function (response) {
          t.message = response.data
          t.drugInfo = t.message.map(item => {
            return { value: item.drug_id, label: item.dr_name }
          })
          console.log(response)
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    getAllDrugs () {
      var t = this
      this.$axios({
        method: 'get',
        url: '/Drug/getAllDrug'
      })
        .then(function (response) {
          t.message = response.data
          t.drugInfo = t.message.map(item => {
            return { value: item.drug_id, label: item.dr_name }
          })
          console.log(response)
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    goInput () {
      console.log(this.selectedCureItem + '=====' + this.drugs + this.cardid)
      var t = this
      this.$axios({
        method: 'post', //  get
        url: '/HistoryCure/saveHistoryCure',
        data: {
          cardid: t.cardid,
          cure_method: '',
          p_time: new Date(),
          symptom: t.symptom,
          disease: t.disease,
          cureItem: t.selectedCureItem,
          drug: t.drugs[0]
        }
      })
        .then(function (response) {
          console.log(response)
          // this.$router.push({path: '/Cure/Input'})
          t.$router.push({path: '/Cure/Advice', query: {cardid: this.cardid}})
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    goAdvice () {
      // this.$router.push({path: '/Cure/HistoryCure'})
    }
  }
}
</script>

<style>
.el-aside {
    background-color: rgb(255, 255, 255);
    color: #333;
    height: 600px
  }
</style>



